@import "react-week-calendar/dist/style.css";


.calendar {
    width: 69%;
    height: 100%;
    float: left;
    padding-right: 10px;
    padding-top: 5px;
}

@media only screen and (max-width: 600px) {
    .calendar{
        width: 100%;
        float: none;
    }
}

.weekCalendar {
    position: relative;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
    overflow: hidden;
    padding-top: 28px;
    color: #6b6b6b;
}

.weekCalendar__content {
    position: relative;
    width: auto;
    overflow: auto;
    max-height: 100vh;
  }
  